import axios from "../../config/axios";

const getTlList = (tl) => {
  return { type: "GET_TL_LIST", payload: tl };
};

const startGetTlList = (data) => {
  return (dispatch) => {
    // console.log(data,"tl_redux")
    // const getData = axios.post("/tl_list", data)
    // .then((response) => {
    //   console.log(response.data.tl, "redux-tl-data");
    //   dispatch(getTlList(response.data.tl))

    // });
    const loadData = async () => {
      try {
        // console.log(data,"asdm-id")
        const getData = await axios.post("/tl_list", data);

        // console.log(getData.data.tl, "redux-tl-data");
        dispatch(getTlList(getData.data.tl));
      } catch (err) {
        console.error(err);
      }
    };
    loadData();
  };
};



export { getTlList, startGetTlList };
