import React from "react";
import ReactDOM from "react-dom";
import "./index.css";
import App from "./App";
import reportWebVitals from "./reportWebVitals";
import "bootstrap/dist/css/bootstrap.min.css";
import { BrowserRouter } from "react-router-dom";
import { Provider } from "react-redux";
import {startLogin } from "./component/action/loggedin"
import {startgGetAsmDetail} from "./component/action/asmDetail"


import configureStore from "./component/store/configureStore";

const store = configureStore();

// store.subscribe(()=>{
//       console.log(store.getState(),"store-subscribe")

//   })

  if(localStorage.getItem('asm_token')){
    store.dispatch(startgGetAsmDetail())    
}
const ele = (
  <Provider store={store}>
    <React.StrictMode>
      <BrowserRouter>
        <App />
      </BrowserRouter>
    </React.StrictMode>
  </Provider>
);

ReactDOM.render(ele, document.getElementById('root'));


// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();
