// import axios from "../../config/axios"




const getfseDetail=(fse)=>{
  return {type:"GET_FSE_DETAIL",payload:fse}
}

const startfseDetail=(data)=>{
  return (dispatch)=>{
    // console.log(id,"fse_detail-id_redux")

    const loadData = async () => {
      try {
        const fseDetail = data
        // const fseData = await axios.post("/fse_stats", data)
        // console.log(fseData.data,"fse_detail_redux")
        dispatch(getfseDetail(fseDetail))
      }
      catch(err) {
        console.error(err)
      }
    }
    loadData()
  }
}

export {
  getfseDetail,
}
export default startfseDetail