import axios from "../../config/axios"

const getOnlyTlList=(tl)=>{
  return {type:"GET_ONLY_TL_LIST",payload:tl}

}
const startOnlyGetTlList = (data) => {
  return (dispatch) => {
    const loadData = async () => {
      try {
        // console.log(data,"asdm-id")
        const getData = await axios.post("/tl_list", data);

        // console.log(getData.data.tl, "redux-tl-data");
        dispatch(getOnlyTlList(getData.data.tl));
      } catch (err) {
        console.error(err);
      }
    };
    loadData();
  };
};

export {
  getOnlyTlList,
  startOnlyGetTlList
};