import {combineReducers,createStore,applyMiddleware} from 'redux'
import thunk from 'redux-thunk'

import loggedinReducer from "../reducer/loggedin"
import AsmDetailReducer from '../reducer/asmDetail'
import TlListReducer from "../reducer/tlList"
import asmIDReducer from "../reducer/asmId"
import tlMonthListReducer from "../reducer/tlMonthList"
import fseListReducer from "../reducer/fseList"
import tlDetailReducer from "../reducer/tlDetail"
import fseDetailReducer from "../reducer/fseDetail"
import leadsListReducer from "../reducer/leadsList"

const configureStore=()=>{
    const store=createStore(combineReducers({
      loggedin:loggedinReducer,
      asmDetail:AsmDetailReducer,
      tlList:TlListReducer,
      asmID:asmIDReducer,
      tlMonthList:tlMonthListReducer,
      tlDetail:tlDetailReducer,
      fseList:fseListReducer,
      fseDetail:fseDetailReducer,
      leadsList:leadsListReducer
      
    }),applyMiddleware(thunk))
    return store
}

export default configureStore