import { Route, Redirect } from "react-router-dom";
import "./App.css";
import Dashboard from "./component/Dashboard";
// import DashboardTL from "./component/DashboardTL"
import DashboardFse from "./component/DashboardFse";
import Login from "./component/Login";
import DashboardTl from "./component/DashoardTl";
import AsmHead from "./component/AsmHead";
import { useState, useEffect } from "react";
import {connect} from "react-redux"
import {startLogin} from "./component/action/loggedin"
// import {startgGetAsmDetail} from "./component/action/asmDetail"


function App(props) {
  const [loggedin, setLogin] = useState(true); //initial flase
  const [tlUserId, setTlUserId] = useState("");
  const [fseUserId, setFseUserId] = useState("");

  useEffect(() => {
    const key = localStorage.getItem("asm_token");
    const email_id = localStorage.getItem("asm_token_email");
    if (key && email_id) {
      // setLogin(true); //success
      props.dispatch(startLogin(true))
      
    } else {
      props.dispatch(startLogin(false))
      // setLogin(true); //reload
    }
  }, [props]);
  // console.log(props.loggedin,"props data")

//   if(localStorage.getItem('authToken')){
//     props.dispatch(startgGetAsmDetail())    
// }
if (performance.navigation.type === 1) {
  window.location.href='/dashboard';
}


  return (
    //this.props.loggedin === "true"
    <div className="app">
      {props.loggedin ? (
        <>
          {/* <Redirect to="/dashboard" /> 
          <Redirect to="/dashboard_admin" />
          */}
          <Route path="/dashboard">
            <Dashboard setLogin={props.loggedin} setTlUserId={setTlUserId} />
          </Route>
          {/* <Route path="/dashboard_admin">
            <AsmHead setLogin={setLogin}/>
          </Route> */}
          <Route path="/dashboard_tl">
            <DashboardTl tlUserId={tlUserId} setFseUserId={setFseUserId} />
          </Route>
          {/* <Route path="/dashboard_tl/1" component={DashboardTL} /> */}
          <Route path="/dashboard_fse">
            <DashboardFse fseUserId={fseUserId} />
          </Route>
        </>
      ) : (
        <Route path="/">
          <Login setLogin={props.loggedin} />
        </Route>
      )}
    </div>
  );
}
const mapStateToProps=(state)=>{
  return {
    loggedin:state.loggedin
  }
}

export default connect(mapStateToProps)(App)

// export default App;
