  
const fseListInitialState=[]

const fseListReducer=(state=fseListInitialState,action)=>{
    switch(action.type){
      case "GET_FSE_LIST":{
          return [...action.payload]
      }
      case "CLEAR_FSE_LIST" : {
          return []
      }
      default:{
          return [...state]
      }
  }
}

export default fseListReducer