import axios from "../../config/axios"




const getAsmId=(tl)=>{
  return {type:"SET_ASM_ID",payload:tl}
}

const startAsmId=(data)=>{
  return (dispatch)=>{
    // const getTotal = axios.post("/asm_stats", data  )
    // .then((res) => {
    //     console.log(res.data, "count");
    //     // setAsmData(res.data);
    //     dispatch(getAsmId(res.data))
    //   });
    const loadData = async () => {
      try {
        // console.log(data,"asdm-id")
        const totalScore =await axios.post("/asm_stats", data )

        // console.log(totalScore.data,"asm-redux-data")
        dispatch(getAsmId(totalScore.data))
      }
      catch(err) {
        console.error(err)
      }
    }
    loadData()
  }
}

export {
  getAsmId,
  startAsmId
}