import axios from "../../config/axios";

const getLeadsList = (tl) => {
  return { type: "GET_LEADS_LIST", payload: tl };
};

const startGetLeadsList = (id) => {
  return (dispatch) => {
    // console.log(id,"Leads_id_redux")

    const loadData = async () => {
      try {
        // console.log(id,"leads_id")
        const data = {
          user_id: id,
        };
        // const LeadsList=await axios.post("/Leads_list", data)
        // console.log(LeadsList.data.users,"Leads_list_redux")
        // dispatch(getLeadsList(LeadsList.data.users))
        const getData =await axios.post("/fse_leads", data)
        // console.log(getData.data.leads,"gaatedata")
        dispatch(getLeadsList(getData.data.leads))

        
      } catch (err) {
        console.error(err);
      }
    };
    loadData();
  };
};




export { getLeadsList , startGetLeadsList };

export default startGetLeadsList;