import axios from "../../config/axios"




const getTlDetail=(tl)=>{
  return {type:"GET_TL_DETAIL",payload:tl}
}

const startTlDetail=(id)=>{
  return (dispatch)=>{
    // console.log(id,"Tl_detail-id_redux")

    const loadData = async () => {
      try {
        const data = {
          tl_id: id,
        };
        const tlData = await axios.post("/tl_stats", data)
        // console.log(tlData.data,"Tl_detail_redux")
        dispatch(getTlDetail(tlData.data))
      }
      catch(err) {
        console.error(err)
      }
    }
    loadData()
  }
}

export {
  getTlDetail,
  startTlDetail
}