import axios from "../../config/axios";

const getFseList = (tl) => {
  return { type: "GET_FSE_LIST", payload: tl };
};

const startGetFseList = (id) => {
  return (dispatch) => {
    // console.log(id,"fse_id_redux")

    const loadData = async () => {
      try {
        const data = {
          tl_id: id,
        };
        const fseList=await axios.post("/fse_list", data)
        // console.log(fseList.data.users,"fse_list_redux")
        dispatch(getFseList(fseList.data.users))
        
      } catch (err) {
        console.error(err);
      }
    };
    loadData();
  };
};

const clearFseList = () => {
  return { type: "CLEAR_FSE_LIST" };
};

const startClearFseList = () => {
  return (dispatch) => {
    console.log("celar_id_redux")

    const loadData = async () => {
      try {
        dispatch(clearFseList())
        
      } catch (err) {
        console.error(err);
      }
    };
    loadData();
  };
}


export { getFseList, startGetFseList ,clearFseList  };
export default startClearFseList;
